import React, {  useEffect, useState } from 'react';

import { connectWallet, getCurrentWalletConnected } from "../../util/interact";

import './Nav.css';
import Logo from '../../img/logo.png';
import {
    FaMedium,
    FaTwitterSquare,
    FaInstagram,
    FaDiscord,
} from 'react-icons/fa';

import { Link, NavLink } from 'react-router-dom';



function Nav() {
    const [isActiveBar, setActiveBar] = useState(false);
    const toggleBar = () => {
        setActiveBar(!isActiveBar);
    };
    const [walletAddress, setWallet] = useState("");
    const [status, setStatus] = useState("");
	useEffect(() => {
		async function getInformation() {
			// const count = await loadTotalMintCount();
			// setTotalMintCount(count);
			// setMintCount(1);
			// addSmartContractListener();
            // const message = await loadCurrentMessage();
            // setMessage(message);

			const { address, } = await getCurrentWalletConnected();
			setWallet(address);
			addWalletListener();
		}
		getInformation()
	}, [])

    const connectWalletPressed = async () => {
        const walletResponse = await connectWallet();
        setStatus(walletResponse.status);
        setWallet(walletResponse.address);
      };


	function addWalletListener() {
		if (window.ethereum) {
		  window.ethereum.on("accountsChanged", (accounts) => {
			if (accounts.length > 0) {
				setWallet(accounts[0]);
			} else {
				setWallet("");
			}
		});
		} else {
		}
	}
  

    return (
        <>
            <nav className="nav-box">
                <div className="container">
                    <div className="nav-box-content d-lg-flex align-items-lg-center justify-content-between">
                        <div className="d-flex justify-content-between d-lg-none">
                            <Link to="/">
                                <img src={Logo} alt="" />
                            </Link>
                            <div
                                onClick={toggleBar}
                                className="icon-bar align-self-center">
                                <span
                                    className={
                                        isActiveBar
                                            ? 'icon-bar-one active'
                                            : 'icon-bar-one'
                                    }></span>
                                <span
                                    className={
                                        isActiveBar
                                            ? 'icon-bar-two active my-1'
                                            : 'icon-bar-two my-1'
                                    }></span>
                                <span
                                    className={
                                        isActiveBar
                                            ? 'icon-bar-three active'
                                            : 'icon-bar-three'
                                    }></span>
                            </div>
                        </div>
                        <ul
                            className={
                                isActiveBar
                                    ? 'd-lg-flex align-items-center mt-4 mt-lg-0'
                                    : 'nav-list-mode d-lg-flex align-items-center'
                            }>
                            <li>
                                <NavLink exact to="/" activeClassName="active">
                                    Home
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to="/roadmap" activeClassName="active">
                                    Roadmap
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to="/utility" activeClassName="active">
                                    Utility
                                </NavLink>
                            </li>
                            <li>
                                <a
                                    href="https://medium.com/@nakedheadz/naked-headz-faq-8d0ab624af9a"
                                    target="_blank">
                                    FAQ
                                </a>
                            </li>
                            <li>
                                <NavLink
                                    to="/whitepaper"
                                    activeClassName="active">
                                    Whitepaper
                                </NavLink>
                            </li>
                        </ul>
                        <Link className="big-logo" to="/">
                            <img src={Logo} alt="" />
                        </Link>
                        <div className="d-lg-flex align-items-center nav-last-link justify-content-end">
                            <div
                                className={
                                    isActiveBar
                                        ? ''
                                        : 'nav-list-mode social-link-box'
                                }>
                                <ul className="d-flex align-items-center gap-2 my-5 my-lg-0">
                                    <li>
                                        <a
                                            href="https://medium.com/@nakedheadz"
                                            target="_blank">
                                            <FaMedium
                                                style={{
                                                    height: '30px',
                                                    width: '30px',
                                                }}
                                            />
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            href="https://twitter.com/NakedHeadz"
                                            target="_blank">
                                            <FaTwitterSquare
                                                style={{
                                                    height: '30px',
                                                    width: '30px',
                                                }}
                                            />
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            href="https://www.instagram.com/naked_headz/"
                                            target="_blank">
                                            <FaInstagram
                                                style={{
                                                    height: '30px',
                                                    width: '30px',
                                                }}
                                            />
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            href="https://discord.gg/ZUkbp3AN5H"
                                            target="_blank">
                                            <FaDiscord
                                                style={{
                                                    height: '38px',
                                                    width: '38px',
                                                }}
                                            />
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <ul className={ isActiveBar ? '' : 'nav-list-mode ms-lg-4' }>                                
                                <li>
                                    <button onClick={connectWalletPressed} >
                                        {walletAddress.length > 0 ? (
                                            String(walletAddress).substring(0,6) +
                                            "..." + 
                                            String(walletAddress).substring(38)
                                        ) : (
                                            <span>Connect to Wallet</span>
                                        ) 
                                        }                                       
                                    </button>

                                    {/* <a onClick={connectWalletPressed}>Connect to Wallet</a>    */}
                                    
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </nav>
        </>
    );
}

export default Nav;
